.App{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.couterdiv{
    height: 70%;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-radius: .6rem;
}
.couterdiv1{
    height: 49%;
    width: 100%;
    background-color: rgb(6, 42, 42);
    border-radius: .6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 2rem;
}
.couterdiv2{
    height: 50%;
    width: 100%;
    background-color: rgb(6, 42, 42);
    border-radius: .6rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.couterdiv2 button{
    height: 20%;
    width: 20%;
    background-color: rgb(64, 150, 150);
    border-radius: .6rem;
   text-align: center;
   font-size: 1.4rem;
   border: none;
   box-shadow: 0px 0px 1px 1px white;
}
